rmAction.method ,\n                        url: this.formAction.url,\n                        data: this.form\n                    })\n                        .then((response) => {\n                            this.$root.isLoading = false;\n                            this.$emit('closeModal','submit')\n                        })\n                        .catch((error) => {\n                            this.$root.isLoading = false;\n                            this.errors.record(error.response.data.errors)\n                        })\n                } else {\n                    console.log('error submit!!');\n                    return false;\n                }\n            });\n        },\n        closeModal() {\n            this.$emit('closeModal','close')\n        },\n\n    },\n    mounted() {\n\n    }\n}\n</script>\n<style >\n.preview-uploader .el-upload {\n    border: 1px dashed #d9d9d9;\n    border-radius: 6px;\n    cursor: pointer;\n    position: relative;\n    overflow: hidden;\n}\n.preview-uploader .el-upload:hover {\n    border-color: #409EFF;\n}\n.preview-uploader-icon {\n    font-size: 28px;\n    color: #8c939d;\n    width: 280px;\n    height: 280px;\n    line-height: 280px;\n    text-align: center;\n}\n.preview {\n    width: 280px;\n    height: 280px;\n    display: block;\n}\n.ck-editor__editable {\n    min-height: 200px;\n}\n</style>\n"],"sourceRoot":""}]);
